.Navbar {
    position: fixed;
    padding: 0rem 0rem;
    left: 0;
    right: 0;
    top: 0;
    box-shadow: 0px 3px 7.5px rgba(0, 0 ,0, 0.1);
    background: white;
}

.Navbar img {
    border-radius: 50px;
    width: 2.8rem;
    margin-right: -2rem;
}

.Navbar_content {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 1rem 2rem;
    justify-content: space-between;
}

.Navbar_content button {
    font-weight: bold;
    border-radius: 5px;
    font-size: 16px;
    padding: 0.5rem 1rem;
    border: 0;
    cursor: pointer;
}