.newmessage {
    margin-top: 7rem;
}

.newmessage button {
    font-weight: bold;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    font-size: 16px;
    padding: 1.1rem 2rem;
    border: 0;
    cursor: pointer;
}

.newmessage svg  {
    margin-left: -0.3rem;
}

