.newmessage form button {
    width: 20%;
  }
  

.newmessage form {
    width: 30rem;
    display: flex;
}

.newmessage input {
    background-color: rgb(247, 247, 247);
    line-height: 1.5;
    width: 100%;
    font-size: 1.5rem;
    color: black;
    outline: none;
    border: none;
    padding: 0.6rem 1rem;
  }

  @media screen and (max-width: 414px) {
    .newmessage form {
        width: 20rem;
        margin-left: 2rem;
    }

    .newmessage input {
        line-height: 1rem;
        width: 18rem;
        font-size: 1rem;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
    }

    .newmessage button {
        padding-right: 3rem;
    }

  }