.Chat {
    display: flex;
    flex-direction: row;
    padding: 0rem 1rem;
    padding-top: 1rem;
    background-color: rgb(241, 241, 241);
    border-bottom: 1px rgb(192, 192, 192) solid;
}

.Chat_elements {
    margin: 0rem;
    padding: 0rem;
    padding-left: 1rem;
    display: flex;
    flex-direction: column;
}

.Chat_elements p {
    font-size: 14px;
    margin: 0rem;
    padding: 0rem;
    color: black;
    padding-bottom: 1rem;
}

.Chat_elements h4 {
    margin: 0rem;
    padding: 0rem;
    color: black;
}

img {
    border-radius: 70px;
    width: 2.8rem;
    height: 2.8rem;
}