

.sendMessage {
    box-shadow: 0px 0px 7px rgba(0, 0 ,0, 0.1);
    position: absolute;
    left: 45rem;
    right: 45rem;
    bottom: 0rem;
    padding-bottom: 1.6rem;
    padding-top: 2.1rem;
    background-color: white;

}


.sendMessage button {
    font-weight: bold;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    font-size: 16px;
    padding: 1.1rem 2rem;
    border: 0;
    cursor: pointer;
}

.sendMessage svg  {
    margin-left: -0.3rem;
}

.headingbutton {
    background-color: rgb(231, 231, 231);
    border: none;
    border-radius: 30px;
    width: 2rem;
    height: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-top: 1rem;
}

.heading {
    border-top: 0.1px rgb(201, 201, 201) solid;
    box-shadow: 0px 0px 7px rgba(0, 0 ,0, 0.1);
    position: absolute;
    left: 45rem;
    right: 45rem;
    top: 5.9rem;
    padding: 1.7rem 0rem;
    background-color: white;
    display: flex;
}

.sendMessage form button {
    width: 20%;
  }
  

.sendMessage form {
    width: 30rem;
    display: flex;
}

.sendMessage input {
    background-color: rgb(247, 247, 247);
    line-height: 1.5;
    width: 100%;
    font-size: 1.5rem;
    color: black;
    outline: none;
    border: none;
    padding: 0.6rem 1rem;
  }

.messages {
    flex-direction: column;
    background-color: white;
}

.direct {
    margin-bottom: -10rem;
    height: 40rem;
    overflow-y: scroll;
}


@media screen and (max-width: 414px) {
    h3 {
        margin-left: 1rem;
    }
    .direct {
        margin-top: -3.5rem;
        background-color: white;
        height: 37rem;
        overflow-y: scroll;
        
    }
    .sendMessage form {
        margin-top: 1.5rem;
        width: 20rem;
        margin-left: 2rem;
        background-color: white;
    }

    .sendMessage input {
        line-height: 1rem;
        width: 18rem;
        font-size: 1rem;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
    }

    .sendMessage button {
        padding-right: 3rem;
    }

    .sendMessage {
        left: 0;
        right: 0;
        padding-top: 0rem;
        padding-bottom: 1.3rem;
    }
    .heading {
        top: 5.7rem;
        box-shadow: 7px 5px 8px rgba(0, 0 ,0, 0.1);
        padding: 0rem 0rem;
        left: 0;
        right: 0;
    }

  }