

p {
    max-width: 500px;
    margin-bottom: 12px;
    line-height: 24px;
    padding: 10px 20px;
    border-radius: 25px;
    position: relative;
    color: white;
    text-align: center;
  }
  
  .message {
    padding: 0rem 1rem;
    display: flex;
    align-items: flex;
  }
  
  
  .sent {
    flex-direction: row-reverse;
  }
  
  .sent p {
    color: white;
    background: #0b93f6;
    align-self: flex-end;
  }
  .received p {
    background: #ececec;
    color: black;
  }
  
.message img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 15px 5px;
  }